import "./BannerSection.scss";
import bannerImg01 from "../../assets/images/banner/Banner01.png";
import bannerImg02 from "../../assets/images/banner/Banner02.png";
import bannerImg03 from "../../assets/images/banner/Banner03.png";
import { useEffect, useState, useRef } from "react";
import { Link } from "react-router-dom";
const BannerSection = () => {
    // 0: 1, 1: 2, 2: 3, 3: ( 1)
    const [idx, setIdx] = useState(0);
    //  
    //   (true  , false )
    const [isAnimating, setIsAnimating] = useState(true);
    const timeRef = useRef(null);
    // 1.   (3 idx )
    useEffect(() => {
        timeRef.current = setInterval(() => {
            setIdx((prev) => prev + 1);
            setIsAnimating(true); //     
        }, 3000);
        return () => clearInterval(timeRef.current);
    }, []);
    // 2.    (Clone )
    useEffect(() => {
        //   (3 ) 
        if (idx === 3) {
            // 0.5(CSS transition ) 
            setTimeout(() => {
                setIsAnimating(false); //   ( )
                setIdx(0); //  1( 0)  
            }, 500);
        }
    }, [idx]);
    return (
        <section id="sec-banner">
            <div className="bnr-wrap">
                <div
                    className="bnr-track"
                    style={{
                        // idx  x  (-100%, -200% ...)
                        transform: `translateX(-${idx * 100}%)`,
                        // isAnimating false  transition   
                        transition: isAnimating ? "transform 0.5s ease-in-out" : "none"
                    }}
                >
                    <div className="bnr-sec01">
                        <img src={bannerImg01} alt="  01" />
                    </div>
                    <div className="bnr-sec02">
                        <img src={bannerImg02} alt="  02" />
                    </div>
                    <div className="bnr-sec03">
                        <div className="bnr-btn">
                            <Link to="/guide">  </Link>
                        </div>
                        <img src={bannerImg03} alt="  03" />
                    </div>
                    {/*   1  */}
                    <div className="bnr-sec01 clone">
                        <img src={bannerImg01} alt="  01()" />
                    </div>
                </div>
            </div>
        </section>
    );
};
export default BannerSection;