import { useState,useEffect,useRef} from "react"
import ProductCard from "../components/category/ProductCard";
import Products from "../assets/data/products.json"
import { useParams } from "react-router-dom"
import { gsap } from "gsap/gsap-core";
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import { useGSAP } from '@gsap/react';
import './CategoryPage.scss';

gsap.registerPlugin(ScrollTrigger);

const CategoryPage = () => {
    const [CardList,setCardList] = useState([]);
    const [bestList,setBestList] = useState([]);
    const params = useParams();
    const [CategoryImg,setCategoryImg] = useState();
    const [CategoryColor,setCategoryColor] = useState();
    const [CategoryLine,setCategoryLine] = useState();
    const [Categorytitle,setCategorytitle] = useState();
    const containerRef = useRef(null);
    const prodRef = useRef(null);

//best true  
    useEffect(()=>{
        const items = Products.Product.filter(
        item => item.best === true && item.type === params.category
        );

        const cditem = Products.Product.filter(
        item => item.type === params.category
        );
        let ci;
        let cc;
        let cl;
        let ct;
        switch(params.category){
            case "H": ci="categoryImgH"; cc="best-hcolor"; cl="hbottomLine"; 
                    ct=""; break;
            case "L": ci="categoryImgL"; cc="best-Lcolor"; cl="LbottomLine";ct="";break;
            case "C": ci="categoryImgC"; cc="best-Ccolor"; cl="CbottomLine"; ct="";break;
            case "Y": ci="categoryImgY"; cc="best-Ycolor"; cl="YbottomLine"; ct="";break;
        }
        setCategoryImg(ci);
        setCategoryColor(cc);
        setCategoryLine(cl);
        setCategorytitle(ct);
        setBestList(items);
        setCardList(cditem);
    }, [params.category]);
//gsap  
        useGSAP(() => {
        if (!prodRef.current) return;

        const items = prodRef.current.querySelectorAll(".prod-item");
        if (!items.length) return;

        gsap.from(items, {
            y: 80,
            opacity: 0,
            duration: 1,
            ease: "power3.out",
            stagger: {
            grid: "auto",
            from: "start",
            amount: 1,
            },
            scrollTrigger: {
            trigger: prodRef.current,
            start: "top 85%",
            toggleActions: "play reverse play reverse"
            },
        });

        ScrollTrigger.refresh();
        }, {
        dependencies: [CardList],
        });
    return (
        <div id="category-page">
            <div className="category-wrap">
                <div className="category-top">
                    <div className={CategoryImg}>
                        
                    </div>
                </div>
                <div className= {`category-best ${CategoryLine}`}>
                <p>'s <span className={CategoryColor}>{Categorytitle}</span>  </p>
                <div className="cetegory-bestlist">
                    {
                    bestList.map((item) => (
                    <div className="best-slide-item" key={item.id}>
                    <ProductCard item={item} />
                    </div>
                    )
                    )}
                </div>
                </div>
                <div ref={containerRef}>
                <div className="category-prod" ref={prodRef}>
                    {CardList.map(item => (
                        <div className="prod-item" key={item.id}>
                        <ProductCard item={item} />
                        </div>
                    ))}
                </div>
                </div>
        <div className="info-text">
            <p>    </p>
        </div>
        </div>
        </div>

    )
}

export default CategoryPage