import React, { useMemo } from "react";
import { useParams } from "react-router-dom";
import "./DetailImage.scss";

//    import 
import detailA from "../../assets/images/Detail/detail-A.png"; // 
import detailB from "../../assets/images/Detail/detail-B.png"; // 
import detailC from "../../assets/images/Detail/detail-C.png"; // 
import detailD from "../../assets/images/Detail/detail-D.png"; // 

const DetailImage = () => {
    const { productId } = useParams();
    const idNum = Number(productId);
    //  productId   
    const detailSrc = useMemo(() => {
        const map = {
        31: detailA, // 
        1: detailB,  // 
        21: detailC, // 
        11: detailD, // 
        };
        return map[idNum] || detailB; // fallback: 
    }, [idNum]);

    return (
        <section className="detail-long-image">
        <img src={require(`${detailSrc}`)} alt=" " />
        </section>
    );
    };

export default DetailImage;